import React from 'react'
import './Navigation.css'
import 'tachyons'
import logo from './logo1.png'
const Navigation = ({onRouteChange,page,signoutCall}) => {
    
        if(page==='signup')
        {
            return(
                <div className='white position_set'>
                    <div className=''>
                        <img src={logo} alt='logo' style={{height:"30px", width:"150px",padding:"0px",margin:"15px 10px 10px 10px"}}/>
                     
                    </div>
                    <ul className=''>
                        <li className='grow dim pointer' onClick={ ()=> onRouteChange('signin')}><b>Sign In</b></li>
                        {/* <a href="https://pulkithb.github.io" className='white'><li className='grow dim'><b>My Website</b></li></a> */}
                    </ul>
                </div>
            )
        }
        else if(page==='signin')
        {
            return(
                <div className='white position_set'>
                    <div className=''>
                        <img src={logo} alt='logo'  style={{height:"35px", width:"150px",padding:"0px",margin:"15px 10px 10px 10px"}}/>
                     
                    </div>
                    <ul className=''>
                        <li className='grow dim pointer' onClick={ ()=> onRouteChange('signup')}><b>Sign Up</b></li>
                        {/* <a href="https://pulkithb.github.io" className='white'><li className='grow dim'><b>My Website</b></li></a> */}
                    </ul>
                </div>
            )
        }
        else if(page==='signout'){
            return(
                <div className='white position_set'>
                    <div className=''>
                        <img src={logo} alt='logo'  style={{height:"35px", width:"150px",padding:"0px",margin:"15px 10px 10px 10px"}}/>
                    </div>
                    <ul className=''>
                        <li className='grow dim pointer'  onClick={signoutCall}><b>Sign Out</b></li>
                        {/* <a href="https://pulkithb.github.io" className='white'><li className='grow dim'><b>My Website</b></li></a> */}
                        <li className='grow dim pointer' onClick={ ()=> onRouteChange('favourite')}><b>Favourite</b></li>
                    </ul>
                </div>
            )
        }
        else{
            return(
                <div className='white position_set'>
                    <div className=''>
                        <img src={logo} alt='logo'  style={{height:"35px", width:"150px",padding:"0px",margin:"15px 10px 10px 10px"}}/>
                    </div>
                    <ul className=''>
                        <li className='grow dim pointer'  onClick={signoutCall}><b>Sign Out</b></li>
                        {/* <a href="https://pulkithb.github.io" className='white'><li className='grow dim'><b>My Website</b></li></a> */}
                        <li className='grow dim pointer' onClick={()=>onRouteChange('signout')}><b>Back</b></li>
                    </ul>
                </div>
            )
        }
}

export default Navigation;