import React from 'react'
import './MovieInfo.css'


const MovieInfo = ({movieinfo}) => {
    const movietitle = movieinfo.title.replace(/\s+/g, "-")
    const movietitle_youtube = movieinfo.title.replace(/\s+/g, "+")
    let myflixer = "https://myflixer.to/search/"+movietitle
    let youtube = "https://www.youtube.com/results?search_query="+movietitle_youtube+"+full+movie"
    return(
        <div>
            <div className='movieinfo_arrange white'>
                <div className='tc infocard' >
                    <img src={movieinfo.image}  className='grow wallpaper' alt={movieinfo.title}  />
                    <h1 className='yellow'>{movieinfo.fullTitle}</h1>
                    <p><b>Release Date: {movieinfo.releaseDate}</b></p>
                    <a href={movieinfo.trailer.link}><button className='pa2 ma1 black bg-yellow br2 grow dim'><b>Watch Trailer Now</b></button></a>
                    <a href={myflixer}><button className='pa2 ma1 black bg-yellow br2 grow dim'><b>Watch Movie Free</b></button></a>
                    <a href={youtube}><button className='pa2 ma1 white bg-dark-red br2 grow dim'><b>Watch on YouTube</b></button></a>
                </div>
                <div className=' pa2 ma2 infocard'>
                    <h1 className='yellow'>{movieinfo.fullTitle}</h1>
                    <h3><b className='yellow'>Release Year: </b>{movieinfo.year}</h3>
                    <h3><b className='yellow'>IMDb Rating: </b>{movieinfo.imDbRating}/10</h3>
                    <h3><b className='yellow'>Run Time: </b>{movieinfo.runtimeStr}</h3>
                    <h3><b className='yellow'>Directors: </b>{movieinfo.directors}</h3>
                    <h3><b className='yellow'>Writers: </b>{movieinfo.writers}</h3>
                    <h3><b className='yellow'>Star Cast: </b>{movieinfo.stars}</h3>
                    <h3><b className='yellow'>Plot: </b> {movieinfo.plot}</h3>
                    <h3><b className='yellow'>Awards: </b>{movieinfo.awards}</h3>
                    <h3><b className='yellow'>Box Office Collection (Worldwide): </b>{movieinfo.boxOffice.cumulativeWorldwideGross}</h3>
                    
                </div>
            </div>
            <h1 className="tc yellow pa2 ma5">Full Cast</h1>
            <div className='movieinfo_cast white pa4'>
                {movieinfo.actorList.map((actor) =>{
                    return(
                        <div className='tc grow'>
                            <img src={actor.image}  alt={actor.name} />
                            <h2 className='yellow'>{actor.name}</h2>
                            <h4>{actor.asCharacter}</h4>
                        </div>
                    )
                })}
            </div>
            
        </div>
    )
}

export default MovieInfo