import React from 'react'
import './SearchBar.css'
const SearchBar = ({getMovieRequest,onTypeSearch}) => {
    return(
        <div className='white tc searchbar'>
            <p style={{fontSize:"70px"}}>Search Here</p>
            <input type="text"  style={{width:"80%",height:"38px",maxWidth:"600px"}} className='searchpannel'  onChange={onTypeSearch} />
            <button className='searchbutton bg-yellow black grow dim shadow-5 ma2 br2' onClick={getMovieRequest}><b>Search</b></button>
            
        </div>
    )
}

export default SearchBar;