import React from 'react'
import './Favourite.css'
class Favourite extends React.Component{
    constructor(props){
        super(props)
    }
    render(){
        const fav = this.props.favourite.map((movie,key) =>{
            return(
                <div className='card shadow-5 white'>
                    <img src={movie.image} alt={movie.title} className='grow'/>
                    <h2 className='yellow'>{movie.title}</h2>
                    {/*<p><b>Description: {movie.description}</b></p>*/}
                    <button className='pa2 pointer black bg-yellow tc ma2 br2 grow dim' onClick={()=>this.props.getMovieInfo(movie.id) }><b>Get Movie</b></button>
                    
                    <button className='pa2 pointer white bg-dark-gray tc ma2 br2 grow dim' onClick={()=>this.props.removeFromFavourite(movie.id) }><b>Remove From Favourite</b></button>
                </div>
            ) 
        })
        return(
            <div>
            <h1 className='tc white pa3'>Your Favourite List is here, {this.props.user.name}!</h1>
            {this.props.favourite.length===0?<h2 className='tc red empty'>Empty..!!</h2>
            :
            <div className='grid_class tc'>
                {fav}
                </div>
    }
        </div>
        )
    }
}
/*const Favourite = ({favourite,getMovieInfo,user,removeFromFavourite}) => {
    return(
        
    )
}*/

export default Favourite